#drive1_box{
    font-size: 13px;
    width: 25%;
    height: 100px;
    text-align: center;
    line-height: 100px;
    background: black;
    color: white;
    cursor: pointer;
    transition: all 0.3s;
}

#drive1_box:hover {
	opacity: 80%;
}

.activeform {
    animation: fadein 2s;
    -moz-animation: fadein 2s; /* Firefox */
    -webkit-animation: fadein 2s; /* Safari and Chrome */
    -o-animation: fadein 2s; /* Opera */
}

#searched_item {
    width: 146px;
    background-color: white;
    height: 97px;
    position: absolute;
    margin-left: 91px;
    margin-top: -15px;
    overflow: auto;
    box-shadow: 0 12px 20px -12px rgb(0 0 0 / 35%);
    display: none;
}

#searched_item.active {
    display: block;
}